@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: 'Dugas'; 
  src: url('./fonts/dugas-pro-ultra.woff') format('woff'); 
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Abel'; 
  src: url('./fonts/abel-latin-400-normal.ttf') format('truetype'); 
  font-weight: normal;
  font-style: normal;
}


.header-top {
  height: 100vh;
}

.Page {
  width: 100vw;
}

.slide-left {
  transition: transform 0.3s ease-in-out;
}
.slide-left-active {
  transform: translateX(-280%);
}

.scroll-effect {
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif, 'Dugas', 'Abel';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
